'use strict'

import apiFlightPrice from './core/api-access/api-flight-price'

const postData = {
  tripType: 'RT',
  from: 'REC',  // origem
  to: 'RIO',  // destino
  outboundDate: '2018-01-10', // data de partida
  inboundDate: '2018-01-12', // data de volta
  cabin: 'EC', // classe econômica (EC) ou executiva (EX)
  adults: 2, // adultos
  children: 1, // crianças
  infants: 0 // bebês
}

apiFlightPrice.search(Date.now(), postData)
  .then(res => console.log(res))
  .catch(err => console.error(err))

